export class Constraint {
    id: string;
    /**
     * the type of the constraint
     */
    type?: string;
    /**
     * the human-readable constraint title
     */
    title?: string;
    /**
     * the human-readable constraint description
     */
    description?: string;
    parameters?: { [key: string]: any; };

    constructor(input?: any) {
        if (input) {
            Object.assign(this, input);
        }
    }

}
